@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@200;300;400;500;700&display=swap');


*{
   margin: 0;
   padding: 0;
   font-family: 'Outfit', sans-serif;
   background-color: hsl(217, 54%, 11%);
}


 body{
      width: 100%;
      height: 100vh;

      & #root{
         width: 100%;
         height: 100%;
         display: grid;
         place-items: center;
         

         & .card{
            width: 23%;
            height: 77.5%;
            border-radius: 1vw;
            background-color:  hsl(216, 50%, 16%);
            padding: 1vw;
            box-sizing: border-box;
            margin: 0;

      

            & .top{
               width: 100%;
               height: 55%;
               background-color: transparent;
               border-radius: 1vw;
               background-image: url("./images/image-equilibrium.jpg");
               background-size: 100%;
               background-repeat: no-repeat;
               background-position-y: 100% ;
               position: relative;
               z-index: 20;
               overflow: hidden;

               & .hover{
                  width: 100%;
                  height: 100%;
                  overflow: none;
                  position: relative;
                  display: grid;
                  place-items: center;
                  background-color: transparent;

                  & img{
                  display: none;
                  background-color: transparent;
                  }
               }

               & .hover:hover{
                  background-color: rgba($color: #47e3ee, $alpha: .4);
                  z-index: 10;

                  & img{
                     display: grid;
                  }
                  
               }
            }


            & .bottom{
               width: 100%;
               height: 38%;
               margin-top: 7%;
               background-color: transparent;
               padding-left: 0.5%;


               & .topic{
                  color: rgb(226, 226, 226);
                  background-color: transparent;
                  font-size: 1.3vw;
                  margin-bottom: 6%;
               }

               & .desc{
                  background-color: transparent;
                  font-size: 1vw;
                  font-weight: 400;
                  color: rgb(131, 131, 131);
                  margin-bottom: 6%;
               }
               
               & .eth{
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  background-color: transparent;

                  & h2{
                     font-size: 1vw;
                     background-color: transparent;
                  }


                  & div{
                     background-color: transparent;
                     display: flex;
                     align-items: center;
                     justify-content: space-between;

                     & img{
                        background-color: transparent;
                        width: 17%;
                     }
                  }

                  & .left{
                     width: 30%;

                     & img{
                        width: 10%;
                     }

                     & .price{
                        color: hsl(178, 100%, 50%);
                     }
                  }

                  & .right{
                     width: 28%;

                     & h2{
                        color: rgb(202, 202, 202);
                        font-weight: 500;
                     }
                  }
               }

               & .line{
                  width: 100%;
                  height: 0.05vw;
                  background-color: hsl(215, 32%, 27%);
                  margin-top: 7%;
                  margin-bottom: 3.5%;
               }
               
               & .avatar{
                  display: flex;
                  align-items: center;
                  background-color: transparent;
                  
             

                  & img{
                     width: 8.5%;
                     background-color: transparent;
                  }

                  & h2{
                     background-color: transparent;
                     font-size: 1vw;
                     font-weight: 400;
                     color: white;
                     margin-right: 1%;
                  }

                  & .creation{
                     background-color: transparent;
                     display: flex;
                     width: 100%;

                     & .n{
                        color: rgb(155, 155, 155);
                        margin-left: 5%;
                     }
                  }
               }
            }

          
         }



      }
   }





