@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@200;300;400;500;700&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: 'Outfit', sans-serif;
  background-color: #0d192b;
}

body {
  width: 100%;
  height: 100vh;
}

body #root {
  width: 100%;
  height: 100%;
  display: -ms-grid;
  display: grid;
  place-items: center;
}

body #root .card {
  width: 23%;
  height: 77.5%;
  border-radius: 1vw;
  background-color: #14253d;
  padding: 1vw;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
}

body #root .card .top {
  width: 100%;
  height: 55%;
  background-color: transparent;
  border-radius: 1vw;
  background-image: url("./images/image-equilibrium.jpg");
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: 100%;
  position: relative;
  z-index: 20;
  overflow: hidden;
}

body #root .card .top .hover {
  width: 100%;
  height: 100%;
  overflow: none;
  position: relative;
  display: -ms-grid;
  display: grid;
  place-items: center;
  background-color: transparent;
}

body #root .card .top .hover img {
  display: none;
  background-color: transparent;
}

body #root .card .top .hover:hover {
  background-color: rgba(71, 227, 238, 0.4);
  z-index: 10;
}

body #root .card .top .hover:hover img {
  display: -ms-grid;
  display: grid;
}

body #root .card .bottom {
  width: 100%;
  height: 38%;
  margin-top: 7%;
  background-color: transparent;
  padding-left: 0.5%;
}

body #root .card .bottom .topic {
  color: #e2e2e2;
  background-color: transparent;
  font-size: 1.3vw;
  margin-bottom: 6%;
}

body #root .card .bottom .desc {
  background-color: transparent;
  font-size: 1vw;
  font-weight: 400;
  color: #838383;
  margin-bottom: 6%;
}

body #root .card .bottom .eth {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: transparent;
}

body #root .card .bottom .eth h2 {
  font-size: 1vw;
  background-color: transparent;
}

body #root .card .bottom .eth div {
  background-color: transparent;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

body #root .card .bottom .eth div img {
  background-color: transparent;
  width: 17%;
}

body #root .card .bottom .eth .left {
  width: 30%;
}

body #root .card .bottom .eth .left img {
  width: 10%;
}

body #root .card .bottom .eth .left .price {
  color: #00fff7;
}

body #root .card .bottom .eth .right {
  width: 28%;
}

body #root .card .bottom .eth .right h2 {
  color: #cacaca;
  font-weight: 500;
}

body #root .card .bottom .line {
  width: 100%;
  height: 0.05vw;
  background-color: #2f415b;
  margin-top: 7%;
  margin-bottom: 3.5%;
}

body #root .card .bottom .avatar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: transparent;
}

body #root .card .bottom .avatar img {
  width: 8.5%;
  background-color: transparent;
}

body #root .card .bottom .avatar h2 {
  background-color: transparent;
  font-size: 1vw;
  font-weight: 400;
  color: white;
  margin-right: 1%;
}

body #root .card .bottom .avatar .creation {
  background-color: transparent;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

body #root .card .bottom .avatar .creation .n {
  color: #9b9b9b;
  margin-left: 5%;
}

